@font-face {
    font-family: RobotoCondensed;
    src: url('assets/roboto-condensed.light.ttf');
    font-weight: 800;
}

@font-face {
    font-family: EconomicaBold;
    src: url('assets/economica-bold.ttf');
}

body {
    text-align: center;
    font-family: 'Monsterrat', sans-serif;
}

h1, h2, h3, h4, h5 {
    font-family: 'EconomicaBold';
}

.parallax { 
    height: 440px; 

    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}


.para-1 {
    background-image: url('http://jpcamara.com/wp-content/uploads/2015/02/carousel.jpg');
}

.para-2 {
    background-image: url('https://www.woodiespartyzone.ie/images/casino/las-vegas-party-header.jpg');
}

.para-3 {
    background-image: url('https://www.parqvancouver.com/content/uploads/2019/03/u-spin-party-2-crop-1559180989-2974x1558.jpg');
}


.red-logo {
    width: 20%;
}

.games-wrapper {
    padding: 1rem;
}

.game-container {
    margin: 5px;
    padding-bottom: 20px;
    border-radius: 12px;
    overflow: hidden;
    background-color: rgba(240, 240, 240, .4);
    border-radius: 24px;
    border: 1px solid rgba(220, 220, 220, .8);
}

.game-name {
    padding: 0;
    margin: 0;
    font-size: 20px;
    font-family: 'Monsterrat', sans-serif;
    display: block;
}

.game-new {
    position: absolute;
    top: 10px;
    left: 0;
    font-size: 18px;
    padding: 0px 4px 0px 4px;
    background-color: green;
    color: white;
    pointer-events: none;
    transition: all .2s;
}

.game-item {
    position: relative;
    margin: 1%;
    overflow: hidden;
    height: 140px;
    border-radius: 16px;
}

.game-image {
    display: block;
    height: 140px; 
    width: auto;
    position: relative;
    transition: all .2s;
}

.game-item:hover .game-image {
    transition: all .2s;
    filter: blur(5px);
    transform: scale(1.05);
    cursor: pointer;
}

.game-item:hover .game-new {
    filter: blur(5px);
}

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 140px;
    width: 100%;
    opacity: 0;
    background: rgba(0, 0, 0, .05);
    cursor: pointer;
}

.game-item:hover .overlay {
    transition: all .5s;
    opacity: 1;
}

.game-button {
    color: white;
    background-color: rgb(17, 170, 20);
    font-family: 'Roboto', sans-serif;
    font-size: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: .2rem;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    pointer-events: none;
    width: 90%;
    border-radius: 6px;
}


.expanded-game-container {
    text-align: center;
    position: relative;
    padding: 10px 42px 20px 42px;

}

.expanded-game-container div {
    display: inline-block;
}

.expanded-game-container .cancel-expand {
    position: absolute;
    top: 18px;
    left: 100px;
    font-size: 46px;
    color: blue;
    background: none;
    border: none;
}

.view-num-more-button {
    margin-right: 20px;
    color: blue;
    background: none;
    border: none;
}

.view-num-more-button:click {
    border: none;
}

.view-num-more-button:hover,
.expanded-game-container .cancel-expand:hover { 
    cursor: pointer;
}

.bottom-cancel-expand {
    font-size: 42px;
    font-family: 'Monsterrat', sans-serif;
    font-weight: 400;
    padding: 12px;
    transition: all .2s ease-in-out;
    border-radius: 14px;
    border: 1px solid black;
}

.bottom-cancel-expand:hover {
    transition: all .2s ease-in-out;
    cursor: pointer;
    background: orange;
}